.ng2-c-loader {
  animation: rotate 0.8s infinite;
  border: 4px solid #cccccc;
  border-radius: 50%;
  border-right-color: transparent;
  display: block;
  height: 30px;
  margin: 0 auto;
  width: 30px; }

.ng2-c-loader--small {
  border-width: 3px;
  height: 20px;
  width: 20px; }

.ng2-c-loader--green {
  border-color: #6ec60f;
  border-right-color: transparent; }

@keyframes rotate {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
